.App {
	background-color: #efefef;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	font-family: 'Raleway', sans-serif, Arial;
}

.wrapper {
	max-width: 1200px;
	width: 100%;
	padding-left: 1em;
	padding-right: 1em;
}

.RoboList {
	position: relative;
	width: 100%;
	height: 700px;
	background-color: #ffffff;
	overflow-y: scroll;
	overflow-x: hidden;
	border-radius: 10px;
	border: 3px solid #e2e2e2;
}

/* width */
::-webkit-scrollbar {
	width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
	box-shadow: inset 0 0 5px grey;
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: rgb(102, 102, 102);
	border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgb(102, 102, 102);
}

@media only screen and (max-width: 700px) {
	.wrapper {
		max-width: none;
		padding-left: 0em;
		padding-right: 0em;
	}
}
.searchboxesWrapper {
	z-index: 2;
	position: -webkit-sticky;
	position: sticky;
	top: -1px;
}

.searchBox {
	display: flex;
	justify-content: center;
}
.searchBox input {
	font-size: 1rem;
	width: 98%;
	height: 40px;
	border: none;
	box-sizing: border-box;
	border-bottom: 3px solid #e2e2e2;
	outline: none;
	transition-duration: 1s;
}

.searchBox input:focus {
	border-color: black;
}

.RoboFrame {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	padding: 1em;
	box-sizing: border-box;
	border-bottom: 2px solid #e2e2e2;
	position: relative;
}

.loadingText {
	height: 80%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.RoboFrame:last-of-type {
	border-bottom: none;
}

.portpic {
	margin-top: 1em;
}

.portpic img {
	border: 2px solid #e2e2e2;
	border-radius: 50%;
	width: 120px;
}

.details {
	margin-left: 2em;
}

.name {
	font-weight: 800;
	font-size: 2rem;
	margin-bottom: 0.3em;
	text-align: center;
}

.minordetails {
	margin-left: 1em;
}

.minordetails * {
	margin-bottom: 0.5em;
}

.plusButton {
	margin-left: auto;
}

.innerBox {
	position: relative;
}

button {
	padding: 0;
	border: none;
	background: none;
	position: relative;
	width: 60px;
	height: 60px;
}

.innerBox input {
	position: absolute;
	width: 100%;
	height: 100%;
	opacity: 0;
	z-index: 1;
	cursor: pointer;
}

.innerBox input:checked ~ button span:first-child {
	display: none;
}

button span {
	position: absolute;
	transition: 300ms;
	background: #b3b3b3;
	border-radius: 2px;
}

.innerBox input:hover ~ button span {
	background: black;
}

button span:first-child {
	top: 25%;
	bottom: 25%;
	width: 10%;
	left: 45%;
}

button span:last-child {
	left: 25%;
	right: 25%;
	height: 10%;
	top: 45%;
}

.allTests {
	height: -webkit-min-content;
	height: min-content;
}

.totals,
.totals * {
	margin: 0;
	display: flex;
	line-height: 12px;
}

.test {
	margin-right: 2em;
}

.tagsearch {
	font-size: 1rem;
	width: 15em;
	height: 40px;
	border: none;
	box-sizing: border-box;
	border-bottom: 3px solid #e2e2e2;
	outline: none;
	transition-duration: 0.3s;
}

.tagsearch:focus {
	border-color: #7c7c7c;
}

.tags {
	position: relative;
	background-color: #e2e2e2;
	padding: 0.5em;
	box-sizing: border-box;
	border-radius: 3px;
	margin-left: 0.4em;
}

@media only screen and (max-width: 570px) {
	.RoboFrame {
		flex-flow: column;
		align-items: center;
		justify-content: space-between;
	}

	.portpic {
		margin-bottom: 1em;
	}

	.details {
		margin-left: 0em;
		margin-top: 0.5em;
	}

	.name {
		font-weight: 800;
		font-size: 2rem;
	}

	.minordetails {
		margin-left: 0em;
		text-align: center;
	}

	.plusButton {
		position: absolute;
		top: 0;
		right: 0;
	}

	.allTests {
		display: flex;
		flex-flow: column;
		justify-content: center;
		align-items: center;
	}

	.tagBox {
		margin: 0 auto;
		max-width: 200px;
	}
}

@media only screen and (max-width: 350px) {
	.name {
		font-weight: 800;
		font-size: 1.5rem;
	}
}

.lds-roller {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}
.lds-roller div {
	-webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	        animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	-webkit-transform-origin: 40px 40px;
	        transform-origin: 40px 40px;
}
.lds-roller div:after {
	content: ' ';
	display: block;
	position: absolute;
	width: 7px;
	height: 7px;
	border-radius: 50%;
	background: rgb(153, 153, 153);
	margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
	-webkit-animation-delay: -0.036s;
	        animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
	top: 63px;
	left: 63px;
}
.lds-roller div:nth-child(2) {
	-webkit-animation-delay: -0.072s;
	        animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
	top: 68px;
	left: 56px;
}
.lds-roller div:nth-child(3) {
	-webkit-animation-delay: -0.108s;
	        animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
	top: 71px;
	left: 48px;
}
.lds-roller div:nth-child(4) {
	-webkit-animation-delay: -0.144s;
	        animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
	top: 72px;
	left: 40px;
}
.lds-roller div:nth-child(5) {
	-webkit-animation-delay: -0.18s;
	        animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
	top: 71px;
	left: 32px;
}
.lds-roller div:nth-child(6) {
	-webkit-animation-delay: -0.216s;
	        animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
	top: 68px;
	left: 24px;
}
.lds-roller div:nth-child(7) {
	-webkit-animation-delay: -0.252s;
	        animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
	top: 63px;
	left: 17px;
}
.lds-roller div:nth-child(8) {
	-webkit-animation-delay: -0.288s;
	        animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
	top: 56px;
	left: 12px;
}
@-webkit-keyframes lds-roller {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@keyframes lds-roller {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.tagBox {
	max-width: 1000px;
}

.tags {
	display: inline-block;
}

.closer {
	margin: 0em;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	background-color: rgb(109, 109, 109);
	border-radius: 50%;
	width: 8px;
	height: 8px;
	top: 2px;
	right: 2px;
	font-size: 0.5rem;
	font-weight: 800;
	cursor: pointer;
}

.closer:hover {
	background-color: rgb(245, 79, 79);
}

